@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {

  .navbar {
    @apply
      md:container
      mx-auto
      flex
      items-center
      justify-between
      py-6;

      .brand {
        @apply pl-3 flex items-center;
      }

      .hamburg {
        @apply
          block md:hidden h-10 mr-3;
      }

      nav {
        @apply hidden md:block;
      }

      .nav-ul {
        @apply
        text-gray-700
          mt-3
          md:mt-0
          grid
          grid-cols-2
          md:grid-cols-4
          gap-x-4
          gap-y-2
          md:gap-y-0;
      }
  }

}
